/* ========================================
   TMS PROFESSIONAL DESIGN SYSTEM 2024
   ======================================== */

:root {
  /* ========== MODERN COLOR PALETTE ========== */
  
  /* Dark Theme - Sophisticated Dark Mode */
  --dark-bg: #0A0A0F;
  --dark-surface: #111118;
  --dark-card: #1A1A24;
  --dark-card-hover: #242430;
  --dark-text: #F8FAFC;
  --dark-text-secondary: #CBD5E1;
  --dark-text-muted: #94A3B8;
  --dark-accent: #6366F1;
  --dark-accent-hover: #4F46E5;
  --dark-border: #2D2D3A;
  --dark-border-light: #3A3A4A;
  --dark-shadow: rgba(0, 0, 0, 0.4);
  --dark-shadow-lg: rgba(0, 0, 0, 0.6);
  
  /* Light Theme - Clean Modern Light */
  --light-bg: #FAFBFC;
  --light-surface: #FFFFFF;
  --light-card: #FFFFFF;
  --light-card-hover: #F8FAFC;
  --light-text: #0F172A;
  --light-text-secondary: #475569;
  --light-text-muted: #64748B;
  --light-accent: #6366F1;
  --light-accent-hover: #4F46E5;
  --light-border: #E2E8F0;
  --light-border-light: #F1F5F9;
  --light-shadow: rgba(0, 0, 0, 0.08);
  --light-shadow-lg: rgba(0, 0, 0, 0.12);
  
  /* ========== BRAND COLORS ========== */
  --primary: #6366F1;
  --primary-hover: #4F46E5;
  --primary-light: #A5B4FC;
  --primary-dark: #4338CA;
  
  --secondary: #EC4899;
  --secondary-hover: #DB2777;
  --secondary-light: #F9A8D4;
  
  --accent: #06B6D4;
  --accent-hover: #0891B2;
  --accent-light: #67E8F9;
  
  --success: #10B981;
  --success-hover: #059669;
  --success-light: #6EE7B7;
  
  --warning: #F59E0B;
  --warning-hover: #D97706;
  --warning-light: #FCD34D;
  
  --error: #EF4444;
  --error-hover: #DC2626;
  --error-light: #FCA5A5;
  
  --info: #3B82F6;
  --info-hover: #2563EB;
  --info-light: #93C5FD;
  
  /* ========== MODERN GRADIENTS ========== */
  --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --gradient-secondary: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  --gradient-success: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  --gradient-warning: linear-gradient(135deg, #ffecd2 0%, #fcb69f 100%);
  --gradient-error: linear-gradient(135deg, #ff9a9e 0%, #fecfef 100%);
  --gradient-info: linear-gradient(135deg, #a8edea 0%, #fed6e3 100%);
  
  /* Dark Mode Gradients */
  --gradient-dark: linear-gradient(135deg, #0A0A0F 0%, #1A1A24 100%);
  --gradient-dark-card: linear-gradient(135deg, #1A1A24 0%, #242430 100%);
  --gradient-dark-surface: linear-gradient(135deg, #111118 0%, #1A1A24 100%);
  
  /* Light Mode Gradients */
  --gradient-light: linear-gradient(135deg, #FAFBFC 0%, #F1F5F9 100%);
  --gradient-light-card: linear-gradient(135deg, #FFFFFF 0%, #F8FAFC 100%);
  --gradient-light-surface: linear-gradient(135deg, #F8FAFC 0%, #E2E8F0 100%);
  
  /* Glassmorphism Gradients */
  --glass-light: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.05) 100%);
  --glass-dark: linear-gradient(135deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.05) 100%);
  
  /* ========== NEUMORPHISM COLORS ========== */
  --neu-light: #FFFFFF;
  --neu-dark: #D1D9E6;
  --neu-shadow-light: #FFFFFF;
  --neu-shadow-dark: #A3B1C6;
  
  /* ========== SPACING SYSTEM ========== */
  --spacing-0: 0;
  --spacing-px: 1px;
  --spacing-0_5: 0.125rem;  /* 2px */
  --spacing-1: 0.25rem;     /* 4px */
  --spacing-1_5: 0.375rem;  /* 6px */
  --spacing-2: 0.5rem;      /* 8px */
  --spacing-2_5: 0.625rem;  /* 10px */
  --spacing-3: 0.75rem;     /* 12px */
  --spacing-3_5: 0.875rem;  /* 14px */
  --spacing-4: 1rem;        /* 16px */
  --spacing-5: 1.25rem;     /* 20px */
  --spacing-6: 1.5rem;      /* 24px */
  --spacing-7: 1.75rem;     /* 28px */
  --spacing-8: 2rem;        /* 32px */
  --spacing-9: 2.25rem;     /* 36px */
  --spacing-10: 2.5rem;     /* 40px */
  --spacing-11: 2.75rem;    /* 44px */
  --spacing-12: 3rem;       /* 48px */
  --spacing-14: 3.5rem;     /* 56px */
  --spacing-16: 4rem;       /* 64px */
  --spacing-20: 5rem;       /* 80px */
  --spacing-24: 6rem;       /* 96px */
  --spacing-28: 7rem;       /* 112px */
  --spacing-32: 8rem;       /* 128px */
  
  /* Legacy spacing for compatibility */
  --spacing-xs: var(--spacing-1);
  --spacing-sm: var(--spacing-2);
  --spacing-md: var(--spacing-4);
  --spacing-lg: var(--spacing-6);
  --spacing-xl: var(--spacing-8);
  --spacing-2xl: var(--spacing-12);
  --spacing-3xl: var(--spacing-16);
  
  /* ========== BORDER RADIUS ========== */
  --radius-none: 0;
  --radius-sm: 0.125rem;    /* 2px */
  --radius-base: 0.25rem;   /* 4px */
  --radius-md: 0.375rem;    /* 6px */
  --radius-lg: 0.5rem;      /* 8px */
  --radius-xl: 0.75rem;     /* 12px */
  --radius-2xl: 1rem;       /* 16px */
  --radius-3xl: 1.5rem;     /* 24px */
  --radius-full: 9999px;
  
  /* ========== MODERN SHADOWS ========== */
  --shadow-xs: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  --shadow-inner: inset 0 2px 4px 0 rgba(0, 0, 0, 0.06);
  
  /* Glassmorphism Shadows */
  --glass-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  --glass-shadow-lg: 0 15px 35px 0 rgba(31, 38, 135, 0.5);
  
  /* Neumorphism Shadows */
  --neu-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
  --neu-shadow-inset: inset 20px 20px 60px #bebebe, inset -20px -20px 60px #ffffff;
  
  /* ========== MODERN TRANSITIONS ========== */
  --transition-none: none;
  --transition-all: all 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-colors: color 150ms cubic-bezier(0.4, 0, 0.2, 1), background-color 150ms cubic-bezier(0.4, 0, 0.2, 1), border-color 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-opacity: opacity 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-shadow: box-shadow 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-transform: transform 150ms cubic-bezier(0.4, 0, 0.2, 1);
  
  /* Legacy transitions for compatibility */
  --transition-fast: 0.15s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-normal: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-slow: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  
  /* ========== TYPOGRAPHY SYSTEM ========== */
  --font-sans: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  --font-mono: 'JetBrains Mono', 'Fira Code', 'Monaco', 'Cascadia Code', 'Roboto Mono', monospace;
  --font-serif: 'Playfair Display', 'Georgia', 'Times New Roman', serif;
  
  /* Font Sizes */
  --text-xs: 0.75rem;      /* 12px */
  --text-sm: 0.875rem;     /* 14px */
  --text-base: 1rem;       /* 16px */
  --text-lg: 1.125rem;     /* 18px */
  --text-xl: 1.25rem;      /* 20px */
  --text-2xl: 1.5rem;      /* 24px */
  --text-3xl: 1.875rem;    /* 30px */
  --text-4xl: 2.25rem;     /* 36px */
  --text-5xl: 3rem;        /* 48px */
  --text-6xl: 3.75rem;     /* 60px */
  --text-7xl: 4.5rem;      /* 72px */
  --text-8xl: 6rem;        /* 96px */
  --text-9xl: 8rem;        /* 128px */
  
  /* Font Weights */
  --font-thin: 100;
  --font-extralight: 200;
  --font-light: 300;
  --font-normal: 400;
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;
  --font-extrabold: 800;
  --font-black: 900;
  
  /* Line Heights */
  --leading-none: 1;
  --leading-tight: 1.25;
  --leading-snug: 1.375;
  --leading-normal: 1.5;
  --leading-relaxed: 1.625;
  --leading-loose: 2;
  
  /* Letter Spacing */
  --tracking-tighter: -0.05em;
  --tracking-tight: -0.025em;
  --tracking-normal: 0em;
  --tracking-wide: 0.025em;
  --tracking-wider: 0.05em;
  --tracking-widest: 0.1em;
  
  /* ========== Z-INDEX SYSTEM ========== */
  --z-0: 0;
  --z-10: 10;
  --z-20: 20;
  --z-30: 30;
  --z-40: 40;
  --z-50: 50;
  --z-auto: auto;
  
  /* Legacy z-index for compatibility */
  --z-dropdown: 1000;
  --z-sticky: 1020;
  --z-fixed: 1030;
  --z-modal-backdrop: 1040;
  --z-modal: 1050;
  --z-popover: 1060;
  --z-tooltip: 1070;
  
  /* ========== BLUR EFFECTS ========== */
  --blur-none: 0;
  --blur-sm: 4px;
  --blur-md: 8px;
  --blur-lg: 16px;
  --blur-xl: 24px;
  --blur-2xl: 40px;
  --blur-3xl: 64px;
  
  /* ========== BACKDROP FILTERS ========== */
  --backdrop-blur-sm: blur(4px);
  --backdrop-blur-md: blur(8px);
  --backdrop-blur-lg: blur(16px);
  --backdrop-blur-xl: blur(24px);
  --backdrop-blur-2xl: blur(40px);
  --backdrop-blur-3xl: blur(64px);
}
/* ========================================
   RESET & BASE STYLES
   ======================================== */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  font-family: var(--font-sans);
  font-size: var(--text-base);
  font-weight: var(--font-normal);
  line-height: var(--leading-normal);
  color: var(--light-text);
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 25%, #f1f5f9 50%, #e2e8f0 75%, #f8fafc 100%);
  background-attachment: fixed;
  transition: var(--transition-colors);
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  position: relative;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(99, 102, 241, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(236, 72, 153, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(6, 182, 212, 0.03) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
}

body.dark-mode {
  background: linear-gradient(135deg, #0A0A0F 0%, #111118 25%, #1A1A24 50%, #111118 75%, #0A0A0F 100%);
  color: var(--dark-text);
}

body.dark-mode::before {
  background: 
    radial-gradient(circle at 20% 80%, rgba(99, 102, 241, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(236, 72, 153, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(6, 182, 212, 0.05) 0%, transparent 50%);
}

/* ========================================
   MODERN TYPOGRAPHY SYSTEM
   ======================================== */

h1, h2, h3, h4, h5, h6 {
  font-weight: var(--font-bold);
  line-height: var(--leading-tight);
  margin-bottom: var(--spacing-4);
  color: var(--light-text);
  letter-spacing: var(--tracking-tight);
}

body.dark-mode h1,
body.dark-mode h2,
body.dark-mode h3,
body.dark-mode h4,
body.dark-mode h5,
body.dark-mode h6 {
  color: var(--dark-text);
}

h1 { 
  font-size: var(--text-4xl);
  font-weight: var(--font-extrabold);
  line-height: var(--leading-none);
}

h2 { 
  font-size: var(--text-3xl);
  font-weight: var(--font-bold);
}

h3 { 
  font-size: var(--text-2xl);
  font-weight: var(--font-semibold);
}

h4 { 
  font-size: var(--text-xl);
  font-weight: var(--font-semibold);
}

h5 { 
  font-size: var(--text-lg);
  font-weight: var(--font-medium);
}

h6 { 
  font-size: var(--text-base);
  font-weight: var(--font-medium);
}

p {
  margin-bottom: var(--spacing-4);
  color: var(--light-text-secondary);
  line-height: var(--leading-relaxed);
}

body.dark-mode p {
  color: var(--dark-text-secondary);
}

a {
  color: var(--primary);
  text-decoration: none;
  transition: var(--transition-colors);
}

a:hover {
  color: var(--primary-hover);
  text-decoration: underline;
}

/* ========================================
   MODERN BUTTON SYSTEM
   ======================================== */

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-2);
  padding: var(--spacing-3) var(--spacing-6);
  border: none;
  border-radius: var(--radius-lg);
  font-family: inherit;
  font-size: var(--text-sm);
  font-weight: var(--font-semibold);
  text-decoration: none;
  cursor: pointer;
  transition: var(--transition-all);
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  user-select: none;
  outline: none;
  box-shadow: var(--shadow-sm);
}

.btn:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

.btn:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s ease;
}

.btn:hover:before {
  left: 100%;
}

/* Primary Button */
.btn-primary {
  background: var(--gradient-primary);
  color: white;
  border: 1px solid transparent;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  filter: brightness(1.1);
}

.btn-primary:active {
  transform: translateY(0);
  box-shadow: var(--shadow-sm);
}

/* Secondary Button */
.btn-secondary {
  background: var(--gradient-secondary);
  color: white;
  border: 1px solid transparent;
}

.btn-secondary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  filter: brightness(1.1);
}

/* Success Button */
.btn-success {
  background: var(--gradient-success);
  color: white;
  border: 1px solid transparent;
}

.btn-success:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  filter: brightness(1.1);
}

/* Warning Button */
.btn-warning {
  background: var(--gradient-warning);
  color: var(--light-text);
  border: 1px solid transparent;
}

.btn-warning:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  filter: brightness(1.1);
}

/* Error Button */
.btn-error {
  background: var(--gradient-error);
  color: white;
  border: 1px solid transparent;
}

.btn-error:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  filter: brightness(1.1);
}

/* Outline Button */
.btn-outline {
  background: transparent;
  border: 2px solid var(--light-border);
  color: var(--light-text);
  box-shadow: none;
}

body.dark-mode .btn-outline {
  border-color: var(--dark-border);
  color: var(--dark-text);
}

.btn-outline:hover {
  background: var(--light-card-hover);
  border-color: var(--primary);
  color: var(--primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

body.dark-mode .btn-outline:hover {
  background: var(--dark-card-hover);
}

/* Ghost Button */
.btn-ghost {
  background: transparent;
  color: var(--light-text);
  border: 1px solid transparent;
  box-shadow: none;
}

body.dark-mode .btn-ghost {
  color: var(--dark-text);
}

.btn-ghost:hover {
  background: var(--light-card-hover);
  color: var(--primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

body.dark-mode .btn-ghost:hover {
  background: var(--dark-card-hover);
}

/* Glass Button */
.btn-glass {
  background: var(--glass-light);
  backdrop-filter: var(--backdrop-blur-md);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: var(--light-text);
  box-shadow: var(--glass-shadow);
}

body.dark-mode .btn-glass {
  background: var(--glass-dark);
  border-color: rgba(255, 255, 255, 0.1);
  color: var(--dark-text);
}

.btn-glass:hover {
  background: var(--primary);
  color: white;
  transform: translateY(-2px);
  box-shadow: var(--glass-shadow-lg);
}

/* Neumorphism Button */
.btn-neu {
  background: var(--neu-light);
  border: none;
  color: var(--light-text);
  box-shadow: var(--neu-shadow);
}

body.dark-mode .btn-neu {
  background: var(--dark-card);
  color: var(--dark-text);
  box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.3), -8px -8px 16px rgba(255, 255, 255, 0.05);
}

.btn-neu:hover {
  transform: translateY(-1px);
  box-shadow: 12px 12px 24px rgba(0, 0, 0, 0.15), -12px -12px 24px rgba(255, 255, 255, 0.7);
}

body.dark-mode .btn-neu:hover {
  box-shadow: 12px 12px 24px rgba(0, 0, 0, 0.4), -12px -12px 24px rgba(255, 255, 255, 0.1);
}

/* Button Sizes */
.btn-xs {
  padding: var(--spacing-1) var(--spacing-3);
  font-size: var(--text-xs);
  border-radius: var(--radius-md);
}

.btn-sm {
  padding: var(--spacing-2) var(--spacing-4);
  font-size: var(--text-sm);
  border-radius: var(--radius-md);
}

.btn-lg {
  padding: var(--spacing-4) var(--spacing-8);
  font-size: var(--text-lg);
  border-radius: var(--radius-xl);
}

.btn-xl {
  padding: var(--spacing-5) var(--spacing-10);
  font-size: var(--text-xl);
  border-radius: var(--radius-2xl);
}

/* Button States */
.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
  box-shadow: var(--shadow-sm) !important;
}

.btn:disabled:hover {
  transform: none !important;
  filter: none !important;
}

/* Button Groups */
.btn-group {
  display: inline-flex;
  border-radius: var(--radius-lg);
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}

.btn-group .btn {
  border-radius: 0;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-group .btn:first-child {
  border-top-left-radius: var(--radius-lg);
  border-bottom-left-radius: var(--radius-lg);
}

.btn-group .btn:last-child {
  border-top-right-radius: var(--radius-lg);
  border-bottom-right-radius: var(--radius-lg);
  border-right: none;
}

/* ========================================
   MODERN CARD SYSTEM
   ======================================== */

.card {
  background: var(--light-card);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--light-border);
  transition: var(--transition-all);
  overflow: hidden;
  position: relative;
}

body.dark-mode .card {
  background: var(--dark-card);
  border-color: var(--dark-border);
  box-shadow: var(--shadow-lg);
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-xl);
}

body.dark-mode .card:hover {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
}

/* Glass Card */
.card-glass {
  background: var(--glass-light);
  backdrop-filter: var(--backdrop-blur-lg);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: var(--glass-shadow);
}

body.dark-mode .card-glass {
  background: var(--glass-dark);
  border-color: rgba(255, 255, 255, 0.1);
}

/* Neumorphism Card */
.card-neu {
  background: var(--neu-light);
  border: none;
  box-shadow: var(--neu-shadow);
}

body.dark-mode .card-neu {
  background: var(--dark-card);
  box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.3), -8px -8px 16px rgba(255, 255, 255, 0.05);
}

/* Card Header */
.card-header {
  padding: var(--spacing-6);
  border-bottom: 1px solid var(--light-border);
  background: var(--light-card-hover);
  position: relative;
}

body.dark-mode .card-header {
  border-color: var(--dark-border);
  background: var(--dark-card-hover);
}

.card-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: var(--gradient-primary);
  opacity: 0.3;
}

/* Card Body */
.card-body {
  padding: var(--spacing-6);
}

/* Card Footer */
.card-footer {
  padding: var(--spacing-6);
  border-top: 1px solid var(--light-border);
  background: var(--light-card-hover);
  position: relative;
}

body.dark-mode .card-footer {
  border-color: var(--dark-border);
  background: var(--dark-card-hover);
}

.card-footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: var(--gradient-primary);
  opacity: 0.3;
}

/* Card Variants */
.card-elevated {
  box-shadow: var(--shadow-lg);
}

.card-elevated:hover {
  box-shadow: var(--shadow-2xl);
}

.card-flat {
  box-shadow: none;
  border: 1px solid var(--light-border);
}

body.dark-mode .card-flat {
  border-color: var(--dark-border);
}

.card-interactive {
  cursor: pointer;
  transition: var(--transition-all);
}

.card-interactive:hover {
  transform: translateY(-2px) scale(1.02);
  box-shadow: var(--shadow-xl);
}

/* Card Sizes */
.card-sm {
  border-radius: var(--radius-lg);
}

.card-sm .card-header,
.card-sm .card-body,
.card-sm .card-footer {
  padding: var(--spacing-4);
}

.card-lg {
  border-radius: var(--radius-3xl);
}

.card-lg .card-header,
.card-lg .card-body,
.card-lg .card-footer {
  padding: var(--spacing-8);
}

/* Input System */
.form-group {
  margin-bottom: var(--spacing-lg);
}

.form-label {
  display: block;
  margin-bottom: var(--spacing-sm);
  font-weight: 500;
  color: var(--light-text);
}

body.dark-mode .form-label {
  color: var(--dark-text);
}

.form-input {
  width: 100%;
  padding: var(--spacing-sm) var(--spacing-md);
  border: 2px solid var(--light-border);
  border-radius: var(--radius-md);
  background: var(--light-surface);
  color: var(--light-text);
  font-family: inherit;
  font-size: 0.875rem;
  transition: all var(--transition-fast);
}

body.dark-mode .form-input {
  background: var(--dark-surface);
  border-color: var(--dark-border);
  color: var(--dark-text);
}

.form-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-input::placeholder {
  color: var(--light-text-secondary);
}

body.dark-mode .form-input::placeholder {
  color: var(--dark-text-secondary);
}

/* Table System */
.table {
  width: 100%;
  border-collapse: collapse;
  background: var(--light-card);
  border-radius: var(--radius-lg);
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}

body.dark-mode .table {
  background: var(--dark-card);
}

.table th {
  background: var(--light-card-hover);
  color: var(--light-text);
  font-weight: 600;
  padding: var(--spacing-md);
  text-align: left;
  border-bottom: 1px solid var(--light-border);
}

body.dark-mode .table th {
  background: var(--dark-card-hover);
  color: var(--dark-text);
  border-color: var(--dark-border);
}

.table td {
  padding: var(--spacing-md);
  border-bottom: 1px solid var(--light-border);
  color: var(--light-text);
}

body.dark-mode .table td {
  border-color: var(--dark-border);
  color: var(--dark-text);
}

.table tbody tr:hover {
  background: var(--light-card-hover);
}

body.dark-mode .table tbody tr:hover {
  background: var(--dark-card-hover);
}

/* Utility Classes */
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }

.font-bold { font-weight: 700; }
.font-semibold { font-weight: 600; }
.font-medium { font-weight: 500; }

.text-primary { color: var(--primary); }
.text-secondary { color: var(--secondary); }
.text-success { color: var(--success); }
.text-warning { color: var(--warning); }
.text-error { color: var(--error); }
.text-info { color: var(--info); }

.bg-primary { background: var(--primary); }
.bg-secondary { background: var(--secondary); }
.bg-success { background: var(--success); }
.bg-warning { background: var(--warning); }
.bg-error { background: var(--error); }
.bg-info { background: var(--info); }

.rounded { border-radius: var(--radius-md); }
.rounded-lg { border-radius: var(--radius-lg); }
.rounded-xl { border-radius: var(--radius-xl); }
.rounded-full { border-radius: var(--radius-full); }

.shadow { box-shadow: var(--shadow-md); }
.shadow-lg { box-shadow: var(--shadow-lg); }
.shadow-xl { box-shadow: var(--shadow-xl); }

.p-0 { padding: 0; }
.p-1 { padding: var(--spacing-xs); }
.p-2 { padding: var(--spacing-sm); }
.p-3 { padding: var(--spacing-md); }
.p-4 { padding: var(--spacing-lg); }
.p-5 { padding: var(--spacing-xl); }

.m-0 { margin: 0; }
.m-1 { margin: var(--spacing-xs); }
.m-2 { margin: var(--spacing-sm); }
.m-3 { margin: var(--spacing-md); }
.m-4 { margin: var(--spacing-lg); }
.m-5 { margin: var(--spacing-xl); }

.flex { display: flex; }
.flex-col { flex-direction: column; }
.flex-row { flex-direction: row; }
.items-center { align-items: center; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }
.justify-end { justify-content: flex-end; }

.w-full { width: 100%; }
.h-full { height: 100%; }

.hidden { display: none; }
.block { display: block; }
.inline-block { display: inline-block; }

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes slideIn {
  from { transform: translateX(-100%); }
  to { transform: translateX(0); }
}

@keyframes scaleIn {
  from { transform: scale(0.9); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.animate-fadeIn { animation: fadeIn 0.5s ease-out; }
.animate-slideIn { animation: slideIn 0.3s ease-out; }
.animate-scaleIn { animation: scaleIn 0.2s ease-out; }
.animate-pulse { animation: pulse 2s infinite; }

/* Responsive Design */
@media (max-width: 768px) {
  html { font-size: 14px; }
  
  .btn {
    padding: var(--spacing-sm) var(--spacing-md);
