  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--light-border);
  position: sticky;
  top: 0;
  z-index: var(--z-sticky);
  transition: all var(--transition-normal);
}

body.dark-mode .nav {
  background: var(--dark-card);
  border-color: var(--dark-border);
  box-shadow: var(--shadow-lg);
}

.nav .brand {
