}
th {
  background: var(--light-card);
  font-weight: 600;
}
body.dark-mode th {
  background: var(--dark-card);
  color: var(--dark-text);
}
tr:hover {
  background: rgba(0,0,0,0.05);
}



/* manager */

/* ================= Manager Dashboard ================= */
.manager-dashboard {
  display: flex;
  min-height: 100vh;
}

/* Sidebar */
.sidebar {
  width: 240px;
  background: var(--light-card);
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  transition: background var(--transition-speed);
  box-shadow: 2px 0 6px rgba(0,0,0,0.1);
}

body.dark-mode .sidebar {
  background: var(--dark-card);
}

.sidebar-title {
  font-size: 20px;
  font-weight: 700;
  padding: 0 20px 20px;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}

.sidebar ul {
  list-style: none;
}

.sidebar ul li {
  padding: 14px 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 500;
  color: inherit;
  transition: background var(--transition-speed);
}

.sidebar ul li i {
  font-size: 18px;
}

.sidebar ul li.active,
.sidebar ul li:hover {
  background: rgba(0,0,0,0.08);
}

body.dark-mode .sidebar ul li.active,
body.dark-mode .sidebar ul li:hover {
  background: rgba(255,255,255,0.15);
}

.badge {
  background: crimson;
  color: white;
  border-radius: 12px;
  padding: 2px 8px;
  font-size: 12px;
  margin-left: auto;
}

/* Main content */
.content {
  flex: 1;
  padding: 30px;
  background: var(--light-bg);
  transition: background var(--transition-speed), color var(--transition-speed);
}

body.dark-mode .content {
  background: var(--dark-bg);
}
/* ===== Enrollment Page ===== */
.enroll-card {
  background: var(--light-card);
  border-radius: 12px;
  padding: 15px;
  margin: 12px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: transform 0.2s ease;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}
.enroll-card:hover { transform: scale(1.02); }

body.dark-mode .enroll-card { background: var(--dark-card); }

.enroll-card.pending { border-left: 5px solid black; }

.styled-table {
  width: 100%;
  border-collapse: collapse;
  margin: 12px 0;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.15);
}
.styled-table th, .styled-table td {
  padding: 12px;
  text-align: left;
}
.styled-table thead {
  background: var(--light-card);
}
body.dark-mode .styled-table thead { background: var(--dark-card); }

.styled-table.approved tbody tr { background: #e6ffed; }
.styled-table.rejected tbody tr { background: #ffe6e6; }
